					<view class="productDescriptionContainer">
						<text class="description">{{item.decription}}</text>
						<view class="numberofBoxes" style="align-items: center; margin-top:30rpx;">
							<text class="boxes">{{item.box}}</text>
							<text class="additional">{{item.addition}}</text>
						</view>
					</view>
					
				</view>
				</view>
				<view class="moreDetailsContainer">
					<view class="serviceContainer">
						<view>
							<text class="serviceTimeText">服务时间</text>
							<text class="exactDate">{{item.date}}</text>
						</view>
						<view class="timeLimitContainer">
							<text class="limitText">规定时间</text>
							<text class="timelimit">{{item.limit}}</text>
						</view>
					</view>
					<view class="customeradress">
						<view>
							<text class="adressText">客户地址：</text>
							<text class="exactLocation">{{item.Location}}</text>
						</view>
					<image src="../../static/PinLocation.png" class="pinSize"></image>
					</view>
					<view class="serviceChargeContainer">
						<text class="serveText">服务费</text>
						<view>
							<text class="serviceCurrency">¥</text>
							<text class="serviceAmount">{{item.amount}}</text>
							<text class="centavo">.00</text>
						</view>
					</view>
					<view class="optionsContainer">
						<view style="border-right: 1rpx solid #F4F4F4; padding-right:150rpx">
							<text class="reject" @tap="open">拒绝</text>
