						<view class="gui-flex gui-align-items-center" style="display: flex; justify-content:center">
							<text class="gui-color-black title">不接此单</text>
						</view>
						<view class="childrenContainer">
							<view class="rejectTitleContainer">
								<text class="reasonTitle">请选择拒绝原因</text>
							</view>
							<view class="reasonContainer" v-for="reasons in text">
								<gui-radio style="display: flex; flex-direction:row-reverse; justify-content:space-between">{{reasons.reason}}</gui-radio>
						</view>
						<view>
							<textarea placeholder="若有其他原因，请在此输入" 
							class="gui-textarea gui-bg-gray gui-border-box" style="width:690rpx; height:300rpx; border-radius:20rpx; width:690rpx; height:300rpx; border-radius:20rpx; font-size:28rpx;" />
							</view>
						</view>
						<view class="gui-flex gui-rows gui-space-between gui-align-items-center" style="margin-top: 50rpx;">
							<view style=" display:flex; margin-right: 400rpx; ">
								<view class=" gui-footer-large-button gui-text-center gui-flex gui-text-center gui-align-items-center gui-justify-content-center gui-text;" 
								style="border-bottom-left-radius:50rpx; border-top-left-radius:50rpx; background:#EEEEF4; width:350rpx; height:70rpx; color:#6869AC;" hover-class="gui-tap" @tap.stop="close">
									取消
								</view>
								<view class="gui-footer-large-button gui-text-center gui-flex gui-text-center gui-align-items-center gui-justify-content-center gui-text;" 
								style="border-bottom-right-radius:50rpx; border-top-right-radius:50rpx; background-color:#6869AC; width:350rpx; height:70rpx; color: #FFFFFF; " hover-class="gui-tap">
									确定
								</view>
							</view>
						</view>
					</view>
				</gui-popup>
	</view>
</template>

<script>
	import request from "@/common/request.js"
export default {
	data() {
		return {
			items:[{
				ordernumber:'2021061614',
				decription:'法国原装进口系列ID——ESTOMPE渐变色',
				box:'三盒装',
				addition:'X1',
				date:'2022-06-22',
				limit:'2天',
				Location:'长沙市岳麓区企业广场F4栋3楼',
				amount:'200'
			}],
			text:[{
				reason:'拒绝原因内容一'
			},
			{
				reason:'拒绝原因内容二'
			},
			{
				reason:'拒绝原因内容三'
			}],
			orderInfo:{},
			
		}
		
	},
	async created() {
		const response = await request.orderInfo({orderid:12})
		this.orderInfo = response.data;
		console.log(response.data)
	},
	methods: {
		close: function() {
			this.$refs.guipopup.close();
		},
		open: function() {
			this.$refs.guipopup.open();
		},
		goOrderPage(){
			uni.navigateTo({
				url:'/pages/OrderPage/OrderPage'
			})
		}
	
		
	}
}
</script>

